
.advanteges-title{
    @include visually-hidden;
}

.advanteges-list{
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(210px, auto));
    grid-column-gap: 30px;
    list-style: none;
    padding: 0;
}

.advanteges-item{
    position: relative;
    margin: 15px 0;
    padding: 65px 0 50px 0;
    &:hover .advantege-svg{
        fill: $second;
        transition: .4s all;
    }
    &:hover .advanvtege-image{
        border: 2px solid $second;
        transform: scale(1.1);
        transition: .4s all;
    }
    &:hover .advantege-subtitle{
        color: $second;
        transition: .4s all;
    }
    &:hover .advantege-text{
        color: $white;
        transition: .4s all;
    }
}

.advanvtege-image{
    position: relative;
    border: 2px solid $text-color;
    border-radius: 50%;
    width: 104px;
    height: 104px;
    transition: .3s all;
}

.advantege-svg{
    position: absolute;
    top: 50%;
    left: 50%;
    fill: $text-color;
    width: 60px;
    height: 60px;
    transform: translate(-50%, -50%);
    transition: .3s all;
}

.advantege-subtitle{
    font-size: 18px;
    font-family: "Raleway";
    color: $text-color;
    font-weight: bold;
    text-transform: uppercase;
    line-height: 1.111;
    text-align: left;
    margin-top: 25px;
    transition: .3s all;
}

.advantege-text{
    position: relative;
    font-size: 14px;
    font-family: "Roboto";
    color: $text-color;
    line-height: 1.429;
    text-align: left;
    margin-bottom: 50px;

    &::after{
        content: "";
        width: 70px;
        height: 2px;
        position: absolute;
        background-color: $second;
        left: 0;
        top: 75px;
        transition: .4s all;
    }
}  

@media(max-width:960px){
    .advanteges-list{
        grid-template-columns: repeat(2, 250px);
        grid-template-rows: repeat(2, auto);
        grid-column-gap: 40px;
        justify-content: center;
    }
    .advanteges-item{
        position: relative;
        margin: 15px 0;
        padding: 30px 0 25px 0;
    }
}

@media(max-width:560px){
    .advanteges-list{
        display: grid;
        grid-template-columns: repeat(auto-fit, minmax(260px, auto));
        grid-template-rows: none;
        grid-column-gap: 40px;
    }
    .advanteges-item{
        position: relative;
        display: flex;
        flex-direction: column;
        align-items: center;
        margin: 10px 0;
        padding: 20px 0 15px 0;
        &:hover .advantege-text::after{
            transform: scaleX(2.7);
            transition: .4s all;
        }
    }
    .advantege-text{
    padding: 0 10px;
    }

}

