.banner{
    background: url("../../img/bg-man.jpg")
}

.banner-wrap{
    padding: 250px 0;
}

.banner-info{
    max-width: 580px;
    margin-left: auto;
}

.banner-title{
    font-size: 80px;
    font-family: "Raleway";
    color: rgb(255, 255, 255);
    font-weight: bold;
    text-transform: uppercase;
    line-height: 0.6;
    margin-bottom: 0;
    margin-top: 0;
}

.title-little{
    font-size: 43px;
}

.title-red{
    color: $second;
    margin: 0 8px;
}

.banner-text{
    font-size: 18px;
    font-family: "Raleway";
    color: $text-color;
    font-weight: bold;
    text-transform: uppercase;
}

.banner-btn{
    @include button();
    margin-right:30px;
}

.banner-btn--last{
    @include button($second, transparent, $second);
    margin-right: 0;
}

@media (max-width:768px){
    .banner{
        background: none;
    }
    .banner-wrap{
        padding: 90px 0;
    }
    .banner-info{
        max-width: 540px;
        margin-left: 0;
        padding: 0 10px;
    }
    .banner-title{
        font-size: 56px;
    }
    .title-little{
        font-size: 32px;
    }
    .banner-text{
        font-size: 18px;
    }
}

@media (max-width:560px){
    .banner{
        background: none;
    }
    .banner-wrap{
        padding: 70px 0;
    }
    .banner-info{
        max-width: 320px;
        margin-left: 0;
    }
    .banner-title{
        font-size: 42px;
        margin-bottom: 32px;
    }
    .title-little{
        font-size: 22px;
    }
    .banner-text{
        font-size: 13px;
        margin-bottom: 22px;
    }
    .banner-btn{
        width: 135px;
        margin-right: 15px;
    }
    .banner-btn--last{
        margin-right: 0px;
    }
}