.progress-name{
    font-size: 14px;
    font-family: "Raleway";
    color: $white;
    font-weight: bold;
    text-transform: uppercase;
    line-height: 1.429;
}

.progress-element {
    border:1px solid rgba(225, 225, 225, 0.25);
    border-radius: 3px;
    padding: 2px;
}

.progress-line{
    height: 4px;
    width: 0;
    background-color: $white;
    position: relative;
    transition: 3s all;
}

.progress-procent{
    opacity: 0;
    position: absolute;
    bottom: 10px;
    right: -15px;
    font-size: 11px;
    font-family: "Roboto";
    color: $white;
    font-weight: bold;
    line-height: 16px;
    background-color: $black;
    width: 32px;
    height: 15px;
    border-radius: 2px;
    text-align: center;
    &::after{
        content: "";
        border: 4px solid transparent;
        border-top: 4px solid $black;
        position: absolute;
        top: 15px;
        left: 12px;
    }
}


