.blog-title--top{
    @include visually-hidden;
}

.blog-list{
    padding: 115px 0 150px 0;
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    grid-template-rows: repeat(5, minmax(130px, auto));
    list-style: none;
}

.blog-item{
    position: relative;
    cursor: pointer;
    padding: 30px 20px 50px 20px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    transition: .4s all;

    &:nth-child(1){
        grid-column: span 4;
        grid-row: 1/span 2;
    }
    &:nth-child(2){
        grid-column: span 8;
        grid-row: 1/span 3;
        order: -1;
    }
    &:nth-child(3){
        grid-column: span 5;
        grid-row: 4/span 2;

    }
    &:nth-child(4){
        grid-column: span 3;
        grid-row: 4/span 2;
    }
    &:nth-child(5){
        grid-column: span 4;
        grid-row: 3/span 3;
    }
    &::before{
        content:"";
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background-color: rgba(17,17,17,0.85) ;
        z-index: -1;
        opacity: 0;
        transition: .3s opacity;
    }
    &:hover{
        border: 4px solid $second;
        transition: .4s all;

        &::before{
            z-index: 0;
            opacity: 1;
            transition: .4s opacity;
        }
        & .blog-information{
            z-index: 2;
            transition: .4s opacity;
        }
    }
}

.blog-item-first{
    padding: 30px 0 0 20px;
    &:hover{
        border: none;
        &::before{
            display: none;
        }  
    }
    & .blog-title{
        position: relative;

        &::before{
            content: "";
            position: absolute;
            top: -30px;
            left: 0;
            width: 70px;
            height: 2px;
            background-color: $text-color;
        }
    }
}

.blog-item-second{
    background: url("../../img/img6.jpg");
    background-size: cover;
};
.blog-item-third{
    background: url("../../img/img7.jpg");
    background-size: cover;
};
.blog-item-four{
    background: url("../../img/img8.jpg");
    background-size: cover;
};
.blog-item-five{
    background: url("../../img/img9.jpg");
    background-size: cover;
};

.blog-info{
    position: relative;
    background-color: $black;
    width: 40px;
    height: 40px;
    transition: .4s all;
    margin-bottom: 10px;
    &:hover{
        background-color: $second;
        transition: .4s all;
    }
}

.blog-svg{
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    fill: $white;
    width: 50%;
    height: 50%;
}


.blog-title{
    font-size: 26px;
    font-family: "Raleway";
    color: $white;
    font-weight: bold;
    text-transform: uppercase;
    line-height: 0.933;
    text-decoration: none;
}

.blog-text--title{
    font-size: 14px;
    font-family: "Roboto";
    color: $text-color;
    line-height: 1.429;
}  

.blog-text{
    position: relative;
    font-size: 12px;
    font-family: "Roboto";
    color: rgb(149, 149, 149);
    text-transform: uppercase;
    line-height: 1.167;
    &::after{
        content: "";
        display: block;
        width: 70px;
        height: 2px;
        background-color: $second;
        margin-top: 25px;
    }
}

.blog-text-bold{
    color: $white;
}

@media (max-width:980px){
    .blog-title{
        font-size: 18px;
    }
    .blog-item{
        padding: 30px 10px 50px 20px;
    }
}

@media (max-width:768px){
    .blog{
        & .blog-item{

            padding: 20px 5px 30px 10px;
        }
        & .blog-list{
        grid-template-columns:repeat(2, 1fr);
        grid-template-rows: 1fr;
            & .blog-item{
            grid-column: span 1;
            grid-row: auto;
            }
            & .blog-item-first{
                grid-column: span 2;
            }
            & .blog-item-second{
                order: 0;
            }
        }
    }
}

@media (max-width:480px){
    .blog{
        & .blog-list{
            grid-template-columns:minmax(290px, auto);
            padding: 50px 0 30px 0;
            & .blog-item{
                padding: 15px 5px 15px 10px;
            }
            & .blog-item{
                grid-column: span 1;

            }
        }
    }
}   