.skills{
    background-color: $second;
}

.skills-wrap{
    display: flex;
    padding: 115px 0 80px 0;
}

.skills-left{
    flex-basis: 275px;
    margin-right: 115px;
}

.skills-right{
    flex-grow: 1;
}

.skill-progress{
    padding-right: 10px;
}

.skills-title{
    position: relative;
    font-size: 30px;
    font-family: "Raleway";
    color: $white;
    font-weight: bold;
    text-transform: uppercase;
    line-height: 0.933;

    &::before{
        content: "";
        position: absolute;
        top: -30px;
        left: 0;
        width: 70px;
        height: 2px;
        background-color: $white;
    }
}

.skills-text{
    font-size: 14px;
    font-family: "Roboto";
    color: $white;
    line-height: 1.429;
    margin-bottom: 50px;
}

.skills-btn{
    @include button(transparent, $black, $white);
}

@media(max-width: 1250px){
    .skills-text{
        padding:0 10px;
    }
    .skills-btn{
        margin-left: 10px;
    }
}

@media(max-width: 980px){
    .skills-left{
        margin-right: 25px;
    }
}

@media(max-width: 768px){
    .skills-wrap{
        flex-direction: column;
        padding: 50px 0 45px 0;
    }
    .skills-left{
        margin-right: 0px;
        flex-basis: auto;
        margin-bottom: 25px;
    }
    .skills-text{
        margin-bottom: 25px;
    }
    .skills-right{
        padding: 0 15px;
    }
    .skills-btn{
        max-width: 135px;
    }
}

