.footer{
    background-color: $black;
    width: 100%;
}
.footer-wrap{
    padding: 30px 0;
}

.footer-menu{
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    padding: 0;
}

.footer-menu .menu-link{
    &::before{
        display: none;
    }
}

@media (max-width:480px){
    .footer-menu{
        justify-content: flex-start;
        padding-left: 10px;
    }
}
