$this-block__text-color: rgb(149, 149, 149);
$this-block__button-color: #8dc63f;

.responsive-wrap{
    padding: 100px 0;
    background: url("../../img/bg-two.png");
    background-size: cover;
}

.responsive-info{
    max-width: 360px;
    margin-left: auto;
}

.responsive-title {
    position: relative;
    font-size: 30px;
    font-family: "Raleway";
    color: $white;
    font-weight: bold;
    text-transform: uppercase;
    line-height: 0.933;
    &::before{
        content: "";
        position: absolute;
        top: -30px;
        left: 0;
        width: 70px;
        height: 2px;
        background-color: $text-color;
    }
}

.responsive-text {
    font-size: 14px;
    font-family: "Roboto";
    color: $text-color;
    line-height: 1.429;
}

.responsive-list{
    list-style: none;
}

.reponsive-list-item{
    position: relative;
    font-size: 14px;
    font-family: "Roboto";
    color: $this-block__text-color;
    line-height: 1.286;
    margin-bottom: 25px;
    &::before{
        content: "";
        position: absolute;
        top: 50%;
        left: -20px;
        width: 8px;
        height: 2px;
        background-color: $second;
    }
}

.responsive-btn{
    @include button(transparent, $this-block__button-color, $white);
    &:hover{
        background-color: darken($color: $this-block__button-color, $amount: 15%)
    }
}

@media(max-width:768px){
    .responsive-wrap{
        padding: 50px 0;
        background: none;
    }
    .responsive-info{
        margin-left: 0;
    }
    .responsive-text {
        padding-left: 10px;
    }
    .responsive-btn{
        margin-left: 10px;
    }
}

@media(max-width:480px){
    .responsive-btn{
        max-width: 135px;
    }
}